.menu {
  box-sizing: border-box;
  height: 100vh;
  background-color: #c2c2c2;
  color: $main-text-color;

  text-transform: uppercase;
  font-weight: 600;

  &__top {
    margin-bottom: 24px;
  }

  @include on-tablet {
    margin-bottom: 0;
  }

  &__nav {
    margin-bottom: 40px;
  }

  &__phone-number {
    color: inherit;
    text-decoration: none;
    display: block;
    font-size: 16px;
    line-height: 27px;
    margin-bottom: 16px;
  }

  &__book-a-test-ride {
    display: block;
    width: fit-content;
    color: inherit;
    text-decoration: none;
    font-size: 12px;
    line-height: 16px;
    letter-spacing: 2px;

    padding-bottom: 7px;
    border-bottom: 1px solid $main-text-color;
  }
}
